a {
  color: black;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
  text-indent: 0;
}

ul {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
}

p {
  margin: 0;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
}